@function reduce ($percent)
  @return rgba(mix(#282828, #fff), $percent / 100)

$font-size: calc(0.8em + 0.25vw)
$font-weight: 300
$heading-weight: 300
$line-height: 1.65
$link-color: #68f
$background:  #ffffff 
$color:  #000000 

@import basic
@import darkmode
@import layout
@import code
@import table
@import highlight

body
  display: flex
  min-height: 100vh

h1
  font-size: 3rem

table
  width: 100%

header
  background-color: #000000
  margin: 0
  overflow: visible
  overflow-x: visible
  overflow-y: visible
  background-color: #000000
  vertical-align: baseline
  margin: 0px
  padding: 0px
  border: 0px none
  height: 5rem


header h1
  font-family: Miller,Georgia,serif
  font-size: 3rem
  color: #ffffff
  font-weight: normal
  margin-bottom: 0em


article header
  flex-direction: column
  align-items: inherit

article
  margin-bottom: 1.5em

article h1
  margin-bottom: 0.2em

article footer
  padding: 1em 0




body > h1
  font-size: 1.7em



